export default {
	data() {
		return {
			cateList: ['全部','已预约','进行中','已完成','已取消'],
			cateState: 0,
			currIndex: 0,
			actList: []
		}
	},
	created() {
		this.getList()
	},
	methods: {
		// 获取活动列表
		getList() {
			this.$http.get({
				url: '/v1/rest/personalCenter/myActivityList',
				data: {
					state: this.cateState,
					userId: uni.getStorageSync('userInfo').userId
				}
			}).then(resp => {
				console.log(resp)
				if(resp.status == 200) {
					this.actList = resp.data
				}
			})
		},
		// 切换分类
		changeCate(index) {
			this.currIndex = index
			this.cateState = index
			this.getList()
		},
		// 取消活动
		handleCancel(item) {
			const that = this
			uni.showModal({
				title: '提示',
				content: '确定要取消该条活动吗？',
				success: function (res) {
					if (res.confirm) {
						that.$http.post({
							url: '/v1/rest/pay/refund',
							data: {
								orderNo: item.orderNo
							}
						}).then(resp => {
							console.log(resp)
							if(resp.status == 200) {
								that.getList()
								uni.showToast({
									title: resp.data.msg,
									duration: 2000,
									icon: 'none'
								}); 
							}
						})
					} else if (res.cancel) {}
				}
			});
		},
		// 活动详情
		toDetail(item) {
			uni.navigateTo({
				url: '/pages/activity/activityDetails/activityDetails?actId=' + item.activitiesId
			})
		}
	}
}
